import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";
import MobileNav from "../MobileNav";
import Hamburger from "hamburger-react";
import { useSelector } from "react-redux";
import UserInfo from "../UserInfoNav";

import { useSpring, useTrail } from "@react-spring/core";
import { animated, config } from "@react-spring/web";
import useNav from "../../hooks/useNav";

const Header = ({ className }) => {
  // const exception = ["/login", "/signup"];
  const auth = useSelector((state) => state.userStatus.auth);
  const userInfo = useSelector((state) => state.userStatus.userInfo);
  // const location = useLocation();
  const nav = useNav();
  const history = useHistory();
  useEffect(() => {
    const unlisten = history.listen(() => {
      setIsMobileNavOpen(false);
    });
    return unlisten;
  }, [history]);

  const [headerAnimated] = useSpring(
    {
      transform: "translateY(0)",
      from: { transform: "translateY(-100%)" },
      delay: 1000,
      // ref: headerAnimate,
    },
    []
  );
  // const [navItemAnimated] = useTransition(
  //   nav,
  //   {
  //     from: { transform: "scale(0)", opacity: 0 },
  //     enter: { transform: "scale(1)", opacity: 1 },
  //     leave: { transform: "scale(0)", opacity: 0 },
  //     key: (item) => item.name,
  //     reset: true,
  //     expires: true,
  //     trail: 500,
  //     exitBeforeEnter: true,
  //     delay: 1300,
  //   },
  //   [nav]
  // );
  const navItemAnimated = useTrail(nav.length, {
    opacity: 1,
    transform: "scale(1)",
    config: config.wobbly,
    from: { opacity: 0, transform: "scale(0)" },
    delay: 1300,
    // ref: navItemAnimate,
  });
  // useChain([headerAnimate, navItemAnimate]);
  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);
  // useChain([headerAnimate, navAnimate]);

  // if (exception.includes(location.pathname)) return null;
  return (
    <>
      <animated.section className={`${className} py-0 py-lg-1`} style={headerAnimated}>
        <div className={` header navbar container`}>
          <div className={`header__logo ${isMobileNavOpen ? "invisible" : ""}`}>
            <img src="https://www.sohoceravietnam.com/public/img/logo.png" alt="Era Logo" />
          </div>
          <div className="header__link d-none d-xl-block">
            <nav className="d-flex flex-column flex-lg-row" style={{ columnGap: 4 }}>
              {navItemAnimated.map((props, index) => {
                const { name, link } = nav[index];
                return (
                  <animated.span key={name} style={props}>
                    <Link to={link}>{name}</Link>
                  </animated.span>
                );
              })}
            </nav>
          </div>
          {auth ? (
            <UserInfo {...userInfo?.user} />
          ) : (
            <div className="header__button d-none d-xl-block">
              <nav className="d-flex" style={{ columnGap: 4 }}>
                <Link
                  to="/login"
                  className="button-secondary fw-bold fw-bold header__button__login"
                >
                  Đăng nhập
                </Link>
                <Link to="/signup" className="button-secondary fw-bold header__button__signup">
                  Đăng ký
                </Link>
              </nav>
            </div>
          )}

          <div className="header__mobile-menu__btn d-block d-xl-none d-flex align-content-center">
            <Hamburger
              toggled={isMobileNavOpen}
              toggle={setIsMobileNavOpen}
              duration={0.3}
              color="#fafafa"
            />
          </div>
        </div>
      </animated.section>
      <MobileNav isOpen={isMobileNavOpen} closeNav={() => setIsMobileNavOpen(false)} />
    </>
  );
};

export default styled(Header)`
  z-index: 99;
  position: sticky;
  top: 0;
  /* padding-top: 64px; */
  background-color: ${(props) => props.theme.colorSecondary};
  .header {
    &__mobile-menu {
      &__btn {
        z-index: 3;
      }
    }
    &__logo {
      div {
        height: 50px;
        background-image: url(/images/logo.png);
        width: auto;
      }
      > img {
        width: 300px;
        height: 50px;
        object-fit: cover;
        border: none;
      }
    }
    &__link {
      a {
        font-weight: bold;
        color: white;
        margin: 0 10px;
        &:hover {
          font-weight: bolder;
          color: #f3eaa7;
        }
      }
    }

    &__button {
      &__login {
        background-color: #fafafa;
        color: ${(props) => props.theme.colorSecondary};
      }
      &__signup {
        &:hover {
          color: #fafafa;
        }
      }
      a {
        padding: 10px 20px;
      }
    }
  }
`;
