import { animated, to } from "@react-spring/web";
import React from "react";
import styled from "styled-components";

function Role({ className, color, roleScale, roleOpacity }) {
  function comp(color, txt) {
    return (
      <div
        style={{
          border: "2px solid " + color,
          borderRadius: "50%",
          width: 24,
          height: 24,
          color: color,
        }}
      >
        <i class="fas fa-crown"></i>
      </div>
    );
  }
  return (
    <animated.div
      className={className}
      style={{
        opacity: roleOpacity,
        transform: to([roleScale], (scale) => `scale(${scale}) translate(-50%,-60%)`),
      }}
    >
      
    </animated.div>
  );
}

export default styled(Role)`
  position: absolute;
  font-size: 12px;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  transform: translate(-50%, -60%);
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  color: ${(props) => props.theme.colorSecondary || "yellow"};
  border-radius: 50%;
  background-color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
`;
